import React from "react";
import styled from "@emotion/styled";
import { padding } from "polished";

import colors from "styles/colors";
import { gothamFonts } from "styles/fonts";
import { ButtonSize } from "components/reusable/atoms/buttons/constants";

const Button = styled("div")({
  ...ButtonSize.LARGE,
  ...gothamFonts["button-m"],
  textTransform: "uppercase",
  width: 200,
  height: 55,
  background: colors.black,
  border: `2px solid ${colors.geminiOrange}`,
  color: colors.white,
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  whiteSpace: "nowrap",

  ...padding(18, 24),

  "&:hover": {
    background: colors.geminiOrange,
    textDecoration: "none",
    color: colors.white,
    cursor: "pointer",
  },
});

interface ButtonProps {
  bottomFormWrapperRef: HTMLDivElement | null;
}

const handleClick = ({ bottomFormWrapperRef }: ButtonProps) => {
  if (bottomFormWrapperRef) {
    bottomFormWrapperRef.scrollIntoView();
    const inputEl = bottomFormWrapperRef.querySelector("input");

    if (inputEl) {
      inputEl.focus();
    }
  }
};

const HeaderButton: React.SFC<ButtonProps> = ({ bottomFormWrapperRef }) => (
  <Button
    onClick={() =>
      handleClick({
        bottomFormWrapperRef,
      })
    }
  >
    Request early access
  </Button>
);

export default HeaderButton;
