import React from "react";

import { Row, Column } from "components/Grid";
import Fade from "react-reveal-animation/Fade";
import ScrollRotate from "react-reveal-animation/ScrollRotate";
import Pulse from "react-reveal-animation/Pulse";

import {
  DetailProps,
  DetailVisualProps,
  DetailImageProps,
  VisualType,
} from "components/solution/brex-cash-pages/types";
import {
  Container,
  DetailText,
  Title,
  Body,
  ImageContainer,
  Image,
  StyledDisabledMobileVideo,
  MobileWrapper,
  DesktopWrapper,
} from "./elements";

const ImageComponent: React.SFC<DetailImageProps> = ({
  visualColumns,
  defaultSrc,
  mobile,
  mobileRetina,
  desktop,
  maxWidth,
  boxShadow,
}) => (
  <ImageContainer>
    <Image
      src={defaultSrc}
      srcSet={`
        ${mobile && mobile.path} ${mobile && mobile.width}w,
        ${mobileRetina && mobileRetina.path} ${mobileRetina &&
        mobileRetina.width}w,
        ${desktop && desktop.path} ${desktop && desktop.width}w,
      `}
      sizes={`
        (max-width: 991px) 100vw,
        (max-width: 1680px) ${(100 * visualColumns) / 12}vw,
        ${(1680 * visualColumns) / 12}px
      `}
      maxWidth={maxWidth}
      boxShadow={boxShadow}
    />
  </ImageContainer>
);

const VisualComponent: React.SFC<DetailVisualProps> = ({
  visualColumns,
  visualType,
  imgSrc,
  videoSrc,
}) => {
  switch (visualType) {
    case VisualType.ANIMATED_IMAGE:
      if (imgSrc) {
        return (
          <ScrollRotate method="perc" from={145} to={-90}>
            <ImageComponent visualColumns={visualColumns} {...imgSrc} />
          </ScrollRotate>
        );
      }
      return null;
    case VisualType.PULSE_IMAGE:
      if (imgSrc) {
        return (
          <Pulse>
            <ImageComponent visualColumns={visualColumns} {...imgSrc} />
          </Pulse>
        );
      }
      return null;
    case VisualType.IMAGE:
      if (imgSrc) {
        return <ImageComponent visualColumns={visualColumns} {...imgSrc} />;
      }
      return null;
    case VisualType.VIDEO:
      if (videoSrc) {
        return (
          <ImageContainer>
            <StyledDisabledMobileVideo
              playsInline
              autoPlay
              loop
              muted
              dataSrc={videoSrc.videoPrefix}
              maxWidth={videoSrc.maxWidth}
              boxShadow={videoSrc.boxShadow}
              placeholderImage={videoSrc.mobileImage}
              videoFirstFrame={videoSrc.videoPoster}
            />
          </ImageContainer>
        );
      }
      return null;
    default:
      return null;
  }
};

const AlternatingDetail: React.SFC<DetailProps> = ({
  title,
  body,
  textColumns = 4,
  visualColumns = 5,
  visualType = VisualType.IMAGE,
  imgSrc,
  videoSrc, // overrides imgSrc
  visualFirstDesktop = false,
}) => (
  <Container>
    <Row
      padding={30}
      gutterWidth={26}
      reversedFlexDirection={visualFirstDesktop}
      verticalAlignment="stretch"
    >
      <Column span={1} spanMedium={0} />
      <Column span={textColumns} spanMedium={12}>
        <DesktopWrapper>
          <DetailText>
            <Fade bottom cascade>
              <Title>{title}</Title>
            </Fade>
            <Fade bottom delay={300}>
              <Body>{body}</Body>
            </Fade>
          </DetailText>
        </DesktopWrapper>
        <MobileWrapper>
          <VisualComponent
            visualType={visualType}
            imgSrc={imgSrc}
            videoSrc={videoSrc}
            visualColumns={visualColumns}
          />
        </MobileWrapper>
      </Column>
      {/*
        there may or may not be extra columns of space,
        but can't be negative
      */}
      <Column
        span={Math.max(10 - textColumns - visualColumns, 0)}
        spanMedium={0}
      />
      <Column span={visualColumns} spanMedium={12}>
        <DesktopWrapper>
          <VisualComponent
            visualType={visualType}
            imgSrc={imgSrc}
            videoSrc={videoSrc}
            visualColumns={visualColumns}
          />
        </DesktopWrapper>
        <MobileWrapper>
          <DetailText>
            <Fade bottom cascade>
              <Title>{title}</Title>
            </Fade>
            <Fade bottom delay={300}>
              <Body>{body}</Body>
            </Fade>
          </DetailText>
        </MobileWrapper>
      </Column>
      <Column span={1} spanMedium={0} />
    </Row>
  </Container>
);

export default AlternatingDetail;
