import React from "react";
import { compose, withStateHandlers, withHandlers, lifecycle } from "recompose";

import Layout from "components/layout";
import colors from "styles/colors";

import HeaderButton from "components/solution/brex-cash-pages/HeaderButton";
import Hero from "components/solution/brex-cash-pages/Hero";
import FloatingButton from "components/solution/brex-cash-pages/FloatingButton";
import GeminiAlternatingDetailList from "components/solution/brex-cash-pages/GeminiAlternatingDetailList";
import BottomSection from "components/solution/brex-cash-pages/BottomSection";
import LegalSection from "components/solution/brex-cash-pages/LegalSection";

import previewImage from "assets/img/brex-cash-preview-image.png";

import {
  heroData,
  detailsData,
  bottomPropsGenerator,
  valuePropListProps,
} from "components/solution/ach-payments/messages";

import {
  PageProps,
  PageStateHandlerProps,
  PageStateProps,
  PageHandlerProps,
  PageExternalProps,
} from "components/solution/brex-cash-pages/types";
import IconValuePropList from "components/reusable/organisms/IconValuePropList";

const _AchPayments: React.SFC<PageProps> = ({
  location,
  setHeroFormWrapperRef,
  setBottomFormWrapperRef,
  bottomFormWrapperRef,
  headerCutoff,
  shouldDisplayButton,
}) => (
  <Layout
    location={location}
    title="Instant ACH Payments for Business | Brex"
    description="Instant ACH payments in under a minute with Brex Cash. Setting the new standard for Cash Management."
    whiteContentBackground
    whiteGutterBackground
    customHeaderButton={() => (
      <HeaderButton bottomFormWrapperRef={bottomFormWrapperRef} />
    )}
    headerCutoff={headerCutoff}
    ogImage={previewImage}
    refCode="brexcash"
    removeToast
  >
    <Hero
      {...heroData}
      formId={1231}
      topForm={1232}
      setHeroFormWrapperRef={setHeroFormWrapperRef}
    />
    <FloatingButton
      shouldDisplay={shouldDisplayButton}
      bottomFormWrapperRef={bottomFormWrapperRef}
    />
    <IconValuePropList {...valuePropListProps} flexIconsAndTitle />
    <GeminiAlternatingDetailList {...detailsData} />
    <BottomSection
      {...bottomPropsGenerator}
      formId={1233}
      bottomForm={1234}
      setBottomFormWrapperRef={setBottomFormWrapperRef}
    />
    <LegalSection gutterBackground={colors.offWhite} />
  </Layout>
);

const AchPayments = compose<PageProps, PageExternalProps>(
  withStateHandlers<PageStateProps, PageStateHandlerProps, PageExternalProps>(
    {
      heroFormWrapperRef: null,
      bottomFormWrapperRef: null,
      headerCutoff: -1,
      shouldDisplayButton: false,
    },
    {
      setHeroFormWrapperRef: () => (heroFormWrapperRef) => ({
        heroFormWrapperRef,
      }),
      setBottomFormWrapperRef: () => (bottomFormWrapperRef) => ({
        bottomFormWrapperRef,
      }),
      setHeaderCutoff: () => (headerCutoff) => ({ headerCutoff }),
      setShouldDisplayButton: () => (shouldDisplayButton) => ({
        shouldDisplayButton,
      }),
    },
  ),
  withHandlers<PageProps, PageHandlerProps>({
    calculateHeaderCutoff: ({
      heroFormWrapperRef,
      setHeaderCutoff,
      setShouldDisplayButton,
    }) => () => {
      if (heroFormWrapperRef) {
        const headerCutoff =
          heroFormWrapperRef.offsetTop + heroFormWrapperRef.offsetHeight;
        setHeaderCutoff(headerCutoff);
        setShouldDisplayButton(window.scrollY >= headerCutoff);
      }
    },
  }),
  lifecycle<PageProps, PageStateProps, {}>({
    componentDidMount() {
      window.addEventListener("scroll", this.props.calculateHeaderCutoff);
      window.addEventListener("resize", this.props.calculateHeaderCutoff);
    },
    componentWillUnmount() {
      window.removeEventListener("scroll", this.props.calculateHeaderCutoff);
      window.addEventListener("resize", this.props.calculateHeaderCutoff);
    },
  }),
)(_AchPayments);

export default AchPayments;
