import React from "react";

import { Row, Column } from "components/Grid";

import MarketoForm from "components/solution/brex-cash-pages/MarketoForm";
import integrationDesktopImg from "assets/img/product/business-cash-account/integration-gray.png";
import videoPaths from "assets/video";

import {
  HeroTitleProps,
  TitleText,
  TextType,
  HeroMessagesProps,
} from "components/solution/brex-cash-pages/types";

import {
  Container,
  Wrapper,
  TitleWrapper,
  Primary,
  DetailsOuter,
  DetailsInner,
  VideoContainer,
  StyledDisabledMobileVideo,
  StyledImg,
  HeroWrapper,
  SubTitle,
} from "./elements";

const Title: React.SFC<HeroTitleProps> = ({ titleContent }) => (
  <TitleWrapper>
    {titleContent.map((content: TitleText) =>
      content.type === TextType.PRIMARY ? (
        <Primary key={content.text}>{content.text}</Primary>
      ) : (
        <span key={content.text}>{content.text}</span>
      ),
    )}
  </TitleWrapper>
);

const Hero: React.SFC<HeroMessagesProps> = ({
  title,
  subTitle,
  setHeroFormWrapperRef,
  formId,
  topForm,
  showHeroImage,
}) => (
  <Container>
    <Row padding={30} gutterWidth={26} withTopMargin>
      <Column span={1} spanSmall={0} />
      <Column span={10} spanSmall={12}>
        <HeroWrapper>
          <Wrapper>
            <Title titleContent={title} />
            <DetailsOuter>
              <DetailsInner>
                <SubTitle>{subTitle}</SubTitle>
                <MarketoForm
                  formId={formId}
                  topForm={topForm}
                  setFormWrapperRef={setHeroFormWrapperRef}
                />
              </DetailsInner>
            </DetailsOuter>
          </Wrapper>
          <VideoContainer>
            {showHeroImage && (
              <StyledImg src={integrationDesktopImg} alt="hero" />
            )}
            {!showHeroImage && (
              <StyledDisabledMobileVideo
                preload="auto"
                playsInline
                autoPlay
                loop
                muted
                src={videoPaths.cashPhoneVideo}
              />
            )}
          </VideoContainer>
        </HeroWrapper>
      </Column>
      <Column span={1} spanSmall={0} />
    </Row>
  </Container>
);

export default Hero;
