import React, { RefObject } from "react";
import ReactDOM from "react-dom";
import { disableBodyScroll, clearAllBodyScrollLocks } from "body-scroll-lock";
import {
  compose,
  withStateHandlers,
  withHandlers,
  lifecycle,
  StateHandler,
  StateHandlerMap,
} from "recompose";

import { PageStateProps } from "components/solution/brex-cash-pages/types";

import {
  StyledMarketoForm,
  PlaceholderContainer,
  PlaceholderInput,
  PlaceholderInputText,
  PlaceholderButton,
  ModalOuter,
  ModalInner,
  Title,
  Body,
  BodyLine,
  SignUpLink,
  FormContainer,
  StyledMarketoWrapper,
} from "./elements";

interface StateProps {
  formLoaded: boolean;
  shouldShowModal?: boolean;
  modalRef: RefObject<HTMLDivElement> | null;
}

type StateHandlerProps = StateHandlerMap<StateProps> & {
  showForm: StateHandler<StateProps>;
  showModal: StateHandler<StateProps>;
  closeModal: StateHandler<StateProps>;
};

interface HandlerProps {
  onModalInnerClick: React.MouseEventHandler<HTMLDivElement>;
}

type InternalProps = StateProps & StateHandlerProps & HandlerProps;

interface ExternalProps {
  formId: number;
  setFormWrapperRef?: StateHandler<PageStateProps>;
  modalTitle?: string;
  showModalCardPrompt?: boolean;
  cta?: string;
  topForm?: number;
  bottomForm?: number;
}

type MarketoFormProps = InternalProps & ExternalProps;

interface PlaceholderProps {
  cta: string;
}

const Placeholder: React.SFC<PlaceholderProps> = ({ cta }) => (
  <PlaceholderContainer>
    <PlaceholderInput>
      <PlaceholderInputText>Work email address</PlaceholderInputText>
    </PlaceholderInput>
    <PlaceholderButton>{cta}</PlaceholderButton>
  </PlaceholderContainer>
);

const _MarketoForm: React.SFC<MarketoFormProps> = ({
  formId,
  shouldShowModal,
  showModal,
  closeModal,
  onModalInnerClick,
  setModalRef,
  showForm,
  formLoaded,
  setFormWrapperRef,
  modalTitle = "You\u2019ve requested early access to Brex Cash",
  showModalCardPrompt = true,
  cta = "Request early access",
  topForm,
  bottomForm,
}) => (
  <React.Fragment>
    {shouldShowModal &&
      ReactDOM.createPortal(
        <ModalOuter ref={setModalRef} onClick={closeModal}>
          <ModalInner onClick={onModalInnerClick}>
            <>
              <Title>{modalTitle}</Title>
              <Body>
                <BodyLine>Our team will be in touch soon.</BodyLine>
                {showModalCardPrompt && (
                  <BodyLine>
                    Not a Brex cardholder yet?{" "}
                    <SignUpLink>Sign up for a Brex card</SignUpLink> to bump up
                    your spot on the waitlist.
                  </BodyLine>
                )}
                <FormContainer
                  formId={topForm !== undefined ? topForm : bottomForm}
                  munchkinId="166-VEV-188"
                  successCallback={closeModal}
                  className=""
                  formLoadedCallback={undefined}
                />
              </Body>
            </>
          </ModalInner>
        </ModalOuter>,
        document.body,
      )}
    <StyledMarketoWrapper ref={setFormWrapperRef}>
      <StyledMarketoForm
        munchkinId="166-VEV-188"
        formId={formId}
        className=""
        successCallback={showModal}
        formLoadedCallback={showForm}
        formLoaded={formLoaded}
        hideErrorsUntilSubmit
      />
    </StyledMarketoWrapper>
    {!formLoaded && <Placeholder cta={cta} />}
  </React.Fragment>
);

const MarketoForm = compose<MarketoFormProps, ExternalProps>(
  withStateHandlers<StateProps, StateHandlerProps, ExternalProps>(
    {
      formLoaded: false,
      shouldShowModal: false,
      modalRef: null,
    },
    {
      showForm: () => () => ({ formLoaded: true }),
      showModal: () => () => ({ shouldShowModal: true }),
      setModalRef: () => (modalRef) => ({ modalRef }),
      closeModal: () => () => {
        clearAllBodyScrollLocks();
        return { shouldShowModal: false, showModalStepTwo: false };
      },
    },
  ),
  withHandlers<MarketoFormProps, HandlerProps>(() => ({
    onModalInnerClick: () => (e) => e.stopPropagation(),
  })),
  lifecycle({
    componentDidUpdate(oldProps: MarketoFormProps) {
      const oldModalRef = oldProps.modalRef;
      const { modalRef } = this.props;

      if (oldModalRef === null && modalRef !== null) {
        const current = modalRef.current;
        if (current !== null) {
          disableBodyScroll(current);
        }
      }
    },
  }),
)(_MarketoForm);

export default MarketoForm;
