import styled from "@emotion/styled";

import { IconProps } from "components/product/business-cash-account/types";

//
// There's some sort of Typescript bug happening with Emotion
// In other cases, I can pass props fine to styled components
// that use object notation
//
// But in this case, that breaks Typescript and I need to use
// a template string
// https://github.com/emotion-js/emotion/issues/1298
// I see this issue but the workarounds didn't do anything
//

const Icon = styled("img")<IconProps>(
  ({ iconHeight, iconWidth }) => `
  height: ${iconHeight}px;
  width: ${iconWidth}px;
  objectFit: contain;
`,
);

export default Icon;
