import React from "react";
import styled from "@emotion/styled";
import { padding } from "polished";

import colors from "styles/colors";
import { gothamFonts } from "styles/fonts";
import zIndex from "styles/z-index";
import InternalLink from "components/reusable/atoms/links/InternalLink";
import logo from "assets/img/logos/brex/logo-black.svg";
import { breakPoints } from "components/Grid";

interface ButtonStyleProps {
  shouldDisplay: boolean;
}

interface ButtonClickProps {
  bottomFormWrapperRef: HTMLDivElement | null;
}

type ButtonProps = ButtonStyleProps & ButtonClickProps;

const Header = styled("div")<ButtonStyleProps>(({ shouldDisplay }) => ({
  display: shouldDisplay ? "flex" : "none",
  position: "fixed",
  justifyContent: "space-between",
  top: 0,
  left: 0,
  right: 0,
  padding: 24,
  animation: "fadein 0.2s",
  backgroundColor: colors.white,
  zIndex: zIndex.HEADER,
  borderBottom: `1px solid ${colors.lightGray}`,

  "@keyframes fadein": {
    from: {
      opacity: 0,
    },
    to: {
      opacity: 1,
    },
  },
  [breakPoints.small]: {
    display: "none",
  },
}));

const LogoWrapper = styled("div")({
  paddingLeft: 24,
  [breakPoints.small]: {
    paddingLeft: 0,
  },
});

const Logo = styled("img")({
  height: 37,
  width: "auto",
  marginTop: 5,
});

const Button = styled("div")({
  minWidth: 150,
  ...padding(12, 40),
  animation: "fadein 0.2s",
  color: colors.white,
  backgroundColor: colors.geminiOrange,
  ...gothamFonts["button-xl"],
  borderRadius: 40,
  zIndex: zIndex.HEADER,
  cursor: "pointer",

  "@keyframes fadein": {
    from: {
      opacity: 0,
    },
    to: {
      opacity: 1,
    },
  },

  "&:hover": {
    filter: "brightness(1.1)",
  },
  [breakPoints.small]: {
    ...padding(12, 10),
  },
});

const handleClick = ({ bottomFormWrapperRef }: ButtonClickProps) => {
  if (bottomFormWrapperRef) {
    bottomFormWrapperRef.scrollIntoView();
    const inputEl = bottomFormWrapperRef.querySelector("input");

    if (inputEl) {
      inputEl.focus();
    }
  }
};

const FloatingButton: React.SFC<ButtonProps> = ({
  shouldDisplay,
  bottomFormWrapperRef,
}) => (
  <Header shouldDisplay={shouldDisplay}>
    <LogoWrapper>
      <InternalLink to="/">
        <Logo src={logo} alt="Brex" />
      </InternalLink>
    </LogoWrapper>
    <Button
      onClick={() =>
        handleClick({
          bottomFormWrapperRef,
        })
      }
    >
      Request early access
    </Button>
  </Header>
);

export default FloatingButton;
